require('styles/Projects.scss');

import React from 'react';

let projects = [
    {
    'name':'',
    'descript':'15',
    'tool':'Zend Studio',
    'method':'',
    'technology':'phpThinkPHPrbacecharts'    
},{
    'name':'3D',
    'descript':'3D',
    'tool':'sublime',
    'method':'',
    'technology':'Three.js 3D'    
},{
    'name':'HTML5',
    'descript':'canvas',
    'tool':'sublime',
    'method':'AI',
    'technology':'canvasjs'    
},{
    'name':'jsLibrary',
    'descript':'js',
    'tool':'sublime',
    'method':'ajaxnpm',
    'technology':'js'    
},{
    'name':'binaryTree',
    'descript':'JS',
    'tool':'sublime',
    'method':'jsnpm',
    'technology':'js'    
},{
    'name':'',
    'descript':'',
    'tool':'sublime',
    'method':'',
    'technology':'js,CSS3'    
},{
    'name':'React',
    'descript':'react',
    'tool':'sublime',
    'method':'',
    'technology':'webpackyomanreactES6SCSS'    
}
]

class ProjectItem extends React.Component{
    render(){
        let projects = this.props.info;
        return(
            <section className="project">
               <header>{projects.name}</header>
               <div className="row">
                     <div className="col-1">:</div>
                     <div className="col-11">{projects.descript}</div>
               </div> 
               <div className="row">
                     <div className="col-1">:</div>
                    <div className="col-11">{projects.tool}</div>
               </div> 
               <div className="row">
                     <div className="col-1">:</div>
                    <div className="col-11">{projects.method}</div>
               </div> 
                <div className="row">
                     <div className="col-1">:</div>
                    <div className="col-11">{projects.technology}</div>
               </div> 
            </section>
        )
    }
}

class ProjectsComponent extends React.Component{
    render(){
        let list = [];
        projects.forEach((val,index)=>{
            list.push(<ProjectItem info={val} key={index}/>)
        })
        return(
            <div className="content">
                {list}
            </div>
        )
    }
}

export default ProjectsComponent;


