import React from 'react';

let skills =                ['985',
							'PCH5web3D',
							'WEBES6',
							'JavaScriptjQueryzeptoAngularionicReactjQueryzeptobootstrap',
							'JavaScriptJS',
							'Ajax,RESTful',
							'PhotoshopUI',
							'PC',
							'RequireJS',
							'sassCSS',
							'JSJS',
							'MVCMVPMVVM',
							'gruntgulpwebpack',
							'nodejsexpressmongodbnpm',
							'githublinuxlinuxlinux'
]

class SkillsListComponent extends React.Component{
    render(){
        let skillsItem = [];
        skills.forEach((val,index)=>{
            skillsItem.push(<li key={index}>{val}</li>)
        })
        return(
            <ul>
                {skillsItem}
            </ul>
        )
    }
}

export default SkillsListComponent;


