require('styles/Projects.scss');

import React from 'react';

let jobs = [{
    time:'201512  201611',
    company:'',
    project:'',
    descript:'',
    duty:'WEBUI',
    technology:'gulpsasscssrequireJSjs',
    reason:''
}]

class JobItem extends React.Component{
    render(){
        let jobs = this.props.info;
        return(
            <section className="job">
               <div className="row">
                     <div className="col-1">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</div>
                     <div className="col-11">{jobs.time}</div>
               </div> 
               <div className="row">
                     <div className="col-1">:</div>
                    <div className="col-11">{jobs.company}</div>
               </div> 
               <div className="row">
                     <div className="col-1">:</div>
                    <div className="col-11">{jobs.project}</div>
               </div> 
                <div className="row">
                     <div className="col-1">:</div>
                    <div className="col-11">{jobs.descript}</div>
               </div>
                <div className="row">
                     <div className="col-1">:</div>
                    <div className="col-11">{jobs.duty}</div>
               </div>
               <div className="row">
                     <div className="col-1">:</div>
                    <div className="col-11">{jobs.technology}</div>
               </div>
               <div className="row">
                     <div className="col-1">:</div>
                    <div className="col-11">{jobs.reason}</div>
               </div>  
            </section>
        )
    }
}

class JobsComponent extends React.Component{
    render(){
        let list = [];
        jobs.forEach((val,index)=>{
            list.push(<JobItem info={val} key={index}/>)
        })
        return(
            <div className="content">
                {list}
            </div>
        )
    }
}

export default JobsComponent;


